.navbar{
    display: flex;
    justify-content: space-around;
    width: 100vw;
    padding-top: 1em;
    padding-bottom: 1em;
    color: black;
    background-color: white;
}

.navbar ul {
    list-style-type: none;
    display: flex;
    color:gray;
    /* flex-wrap: wrap; */
}

.navbar > div{
    display: flex;
    flex-direction: row;
}

/* Navbar Items */

.navbar__links, .navbar__actions {
    display: flex;
    flex-direction: row;
    list-style-type: none;
}

.navbar li{
    text-decoration: none;
    padding: 0.4em 1em;
}

.navbar__tabs li *{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.navbar__tabs img{
    display: block;
    width: 24px ;
    height: 24px;
}

/* D */

.dropdown__btn{
    background: none;
    border: none;
    cursor: pointer;
}   

.dropdown__btn::after{
    content: '|';
    position: absolute;
}


.prem__adv{
    display: flex;
    flex-direction: column;
    color: rgb(155, 114, 11);
}